import { EmployeeEditComponent } from './components/employeeedit.component';
import { EmployeeListComponent } from './components/employeelist.component';
import { PersistanceService } from './services/persistance.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { InrCurrencyPipe } from 'app/pipes/inrcurrency.pipe';
import { EmployeeAddComponent } from 'app/components/employeeadd.component';
import { EmployeeDetailComponent } from 'app/components/employeedetail.component';

const AppRoutes: Routes = [
  {
    path:'employee',
    children:[
      {
        path:'list',
        component:EmployeeListComponent
      },
      {
        path:'add',
        component:EmployeeAddComponent
      },
      {
        path:'edit/:empid',
        component:EmployeeEditComponent
      },
      {
        path:'detail/:empid',
        component:EmployeeDetailComponent
      }
    ]
  },
  { 
    path: '',
    redirectTo: '/employee/list',
    pathMatch: 'full'
  }
];

@NgModule({
  declarations: [
    AppComponent, EmployeeListComponent, EmployeeAddComponent, EmployeeEditComponent, EmployeeDetailComponent, InrCurrencyPipe
  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    RouterModule.forRoot(AppRoutes)
  ],
  providers: [{provide:PersistanceService, 
    useFactory:PersistanceServiceFactory}],
  bootstrap: [AppComponent]
})
export class AppModule { }

export function PersistanceServiceFactory() {
  return new PersistanceService('assignment-employeelist');
}



