import { Employee } from '../services/employee.model';
import { ActivatedRoute } from '@angular/router';
import { PersistanceService } from '../services/persistance.service';
import { Component } from '@angular/core';
@Component({
    templateUrl:'./employeedetail.component.html',
    styles: [`
        .employeeUnit {
            border:1px solid #444;
        }
        .employeeUnit img {
            width:128px;
        }
        .employeeUnit .details {
            margin-top:8px;
        }
        .employeeUnit .details p:first-child {
            font-size:28px;
        }
        .employeeUnit .editbtn {
            margin-top:20px;
        }
        .detailContactInfo {
            border-top:1px solid #444;
            font-size:18px;
        }
        .detailContactInfo .row {
            padding-top:10px;
            padding-bottom:10px;
        }
    `]
})
export class EmployeeDetailComponent {
    private emp:Employee;
    constructor(protected persistanceService: PersistanceService, 
        protected currentRoute: ActivatedRoute) { }

    ngOnInit() {
        let empId = this.currentRoute.snapshot.params['empid'];
        this.emp = this.persistanceService.GetEmployee(empId);
    }
}


