import { PersistanceService } from '../services/persistance.service';
import { Employee } from '../services/employee.model';
import { ChangeDetectionStrategy, Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';

@Component({
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './employeelist.component.html',
    styles:[`
        .employeelist-container {
            margin-top:15px;
        }
        .employeelist-container > .col-md-12 > .row {
            margin-top:10px;
            margin-bottom:10px;
        }

        .employeelist-container label {
            padding:8px 0;
        }

        .employeeListTable {
            border-left:1px solid #444;
            border-right:1px solid #444;
        }

        .tableHeader {
            background-color:#444;
            color:#fff;
            padding-top:5px;
            padding-bottom:5px;
        }

        .tableBody {
            background-color:#efefef;
        }

        .tableBody .employeeUnit {
            border-bottom:1px solid #444;
        }

        .employeeUnit img {
            width:96px;
        }

        .employeeUnit .details {
            margin-top:8px;
        }

        .employeeUnit > .details:last-child p:last-child {
            padding:5px 10px;
            background-color:#444;
            color:#fff;
            display:inline-block;
            border-radius:30px;
        }

        .dpt .dptDetails {
            margin-top:10px;
        }

        .dpt .dptEdit {
            margin-top: 30px;
        }

        .dpt .dptEdit a {
            font-size:20px;
            margin-left:5px;
            margin-right:5px;
            color:#444;
            text-decoration:none;
        }

        .dpt .dptEdit a:hover {
            color:#888;
            cursor:pointer;
        }
    `]
})
export class EmployeeListComponent implements OnInit, OnDestroy {
    public list:Array<Employee>;
    private employeeListSubscription: Subscription;
    public viewList:Array<Employee>;

    constructor(private persistanceService: PersistanceService) {
        this.list = [];
        this.viewList = [];
    }

    public filterChanged(deptName: string, searchTerm: string, sortDirection: string) {
        let emp: Employee[] = this.list.filter(e => {
            if(deptName == '' || e.Department == deptName) {
                return e;
            }
        });
        emp = emp.filter(e => {
            if(searchTerm == '' || e.FirstName.toLowerCase().startsWith(searchTerm)) {
                return e;
            }
        });
        if(sortDirection == 'lth')
            emp = emp.sort(this.sortBySalaryLowToHigh);
        else
            emp = emp.sort(this.sortBySalaryHighToLow);
        
        this.viewList = emp;
    }

    ngOnInit() {
        this.employeeListSubscription 
        = this.persistanceService.EmployeeListObservable.subscribe(el => {
            this.list = el;
            this.viewList = el;
            this.viewList = this.viewList.sort(this.sortBySalaryLowToHigh);
        }, err => {
            console.log(err);
        }, () => {
            this.employeeListSubscription.unsubscribe();
        });
    }

    ngOnDestroy() {
        if(this.employeeListSubscription.closed == false) {
            this.employeeListSubscription.unsubscribe();
        }
    }

    private sortBySalaryLowToHigh(a: Employee, b: Employee): number {
        if(a.Salary < b.Salary)
            return -1;
        else if(a.Salary == b.Salary)
            return 0;
        else
            return 1;
    }

    private sortBySalaryHighToLow(a: Employee, b: Employee): number {
        if(a.Salary > b.Salary)
            return -1;
        else if(a.Salary == b.Salary)
            return 0;
        else
            return 1;
    }

    private deleteEmployeeClicked(empid:number): void {
        this.persistanceService.DeleteEmployee(empid);
    }
}


