import { Router } from '@angular/router';
import { Employee } from '../services/employee.model';
import { PersistanceService } from '../services/persistance.service';
import { Component } from '@angular/core';
import { constructDependencies } from '@angular/core/src/di/reflective_provider';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
    templateUrl: './employeeform.html'
})
export class EmployeeAddComponent {
    public employeeForm:FormGroup;
    public days:number[];
    public years:number[];

    constructor(protected persistanceService: PersistanceService, protected router: Router) {
        this.employeeForm = new FormGroup({
            firstName: new FormControl('', Validators.required),
            lastName: new FormControl('', Validators.required),
            gender: new FormControl('', Validators.required),
            dobDay: new FormControl('', [Validators.required]),
            dobMonth: new FormControl('', [Validators.required]),
            dobYear: new FormControl('', [Validators.required]),
            mobile: new FormControl('', [Validators.required]),
            email: new FormControl('', [Validators.required, Validators.email]),
            address: new FormControl('', Validators.required),
            salary: new FormControl(''),
            department: new FormControl('')
        });

        // fill up days and years
        this.days = [];
        this.years = [];
        for(let i=1; i<=31; i++) {
            this.days.push(i);
        }
        let currentYear:number = new Date(Date.now()).getFullYear();
        for(let i=currentYear; i>currentYear-100; i--) {
            this.years.push(i);
        }
    }

    public formSubmitted(): void {
        
        if(this.validate(this.employeeForm) == false) {
            return;
        }

        let day:number = this.employeeForm.get('dobDay').value;
        let month:number = this.employeeForm.get('dobMonth').value;
        let year:number = this.employeeForm.get('dobYear').value;
        let emp: Employee = new Employee();
        emp.Address = this.employeeForm.get('address').value;
        emp.DateOfBirth = new Date(year, month, day);
        emp.Department = this.employeeForm.get('department').value;
        emp.Email = this.employeeForm.get('email').value;
        emp.FirstName = this.employeeForm.get('firstName').value;
        emp.IsMaleGender = this.employeeForm.get('gender').value == 'male' ? true : false;
        emp.LastName = this.employeeForm.get('lastName').value;
        emp.Mobile = this.employeeForm.get('mobile').value;
        emp.Salary = this.employeeForm.get('salary').value;
        let id = this.persistanceService.AddEmployee(emp);

        this.router.navigateByUrl('/employee/detail/' + id);
    }

    protected cancelClicked(): void {
        this.router.navigateByUrl('/employee/list');
    }

    protected validate(form: FormGroup): boolean {
        let day:number = form.get('dobDay').value;
        let month:number = form.get('dobMonth').value;
        let year:number = form.get('dobYear').value;
        let mobile = form.get('mobile').value;

        let isLeap: boolean = (year % 100 === 0) ? (year % 400 === 0) : (year % 4 === 0);
        if(isLeap && (month == 1) && (day > 29)) {
            alert('Day, month and year combination is invalid.');
            return false;
        }
        if(!isLeap && (month == 1) && (day > 28)) {
            alert('Day, month and year combination is invalid.');
            return false;
        }
        if((month != 1) && (month % 2) == 1 && (day > 30)) {
            alert('Day, month and year combination is invalid.');
            return false;
        }
        if((parseInt(mobile) == NaN) || (parseInt(mobile).toString().length != mobile.length)) {
            alert('Mobile number entered is invalid.');
            return false;
        }

        return true;
    }
}


