import { Employee } from './employee.model';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';

export class PersistanceService {
    // saves and retrives data from localStorage
    private EmployeeList: Array<Employee>;
    private key:string;
    private counter:number;
    public EmployeeListObservable: Observable<Employee[]>;

    constructor(keyString:string) { 
        this.key = keyString;
        this.counter = 0;
        this.EmployeeListObservable = new BehaviorSubject<Employee[]>([]);
    }

    public Load(): void {
        let list = localStorage.getItem(this.key);
        if(list == null) {
            //console.log('employee list not found in local storage');
            this.EmployeeList = [/*{
                Address:'', DateOfBirth:new Date(Date.now()), Department:'Engineering', Email:'shrijit1991@gmail.com',
                FirstName:'Shrijit', Id:100, IsMaleGender:true, LastName:'Pandey',
                Mobile:'+91 987654321', Salary:60000
            }, {
                Address:'', DateOfBirth:new Date(Date.now()), Department:'Human Resource', 
                Email:'swastik.das@gmail.com',
                FirstName:'Swastik', Id:100, IsMaleGender:true, LastName:'Das',
                Mobile:'+91 258741369', Salary:635000
            }*/];
            let objString:string = JSON.stringify(this.EmployeeList);
            localStorage.setItem(this.key, objString);
        }
        else {
            /*console.log('employee list found in local storage');
            console.log(list);*/
            this.EmployeeList = JSON.parse(list);
            (<BehaviorSubject<Employee[]>> this.EmployeeListObservable).next(this.EmployeeList);
        }
    }

    public UnLoad(): void {
        (<BehaviorSubject<Employee[]>> this.EmployeeListObservable).complete();
    }

    public AddEmployee(employee: Employee): number {
        let emp: Employee = employee;
        emp.Id = this.counter;
        this.counter += 1;
        this.EmployeeList.push(emp);
        localStorage.setItem(this.key, JSON.stringify(this.EmployeeList));
        (<BehaviorSubject<Employee[]>> this.EmployeeListObservable).next(this.EmployeeList);
        return emp.Id;
    }

    public DeleteEmployee(empId: number): void {
        let index = -1;
        this.EmployeeList.find((ele, ind) => {
            if(ele.Id == empId) {
                index = ind;
                return true;
            }
            else {
                return false;
            }
        });
        if(index > -1 && index < this.EmployeeList.length) {
            this.EmployeeList.splice(index, 1);
            localStorage.setItem(this.key, JSON.stringify(this.EmployeeList));
            (<BehaviorSubject<Employee[]>> this.EmployeeListObservable).next(this.EmployeeList);
        }
    }

    public UpdateEmployee(empId: number, emp: Employee): void {
        let index = -1;
        this.EmployeeList.find((ele, ind) => {
            if(ele.Id == empId) {
                index = ind;
                return true;
            }
            else {
                return false;
            }
        });
        if(index > -1 && index < this.EmployeeList.length) {
            this.EmployeeList[index] = emp;
            this.EmployeeList[index].Id = empId;
            localStorage.setItem(this.key, JSON.stringify(this.EmployeeList));
            (<BehaviorSubject<Employee[]>> this.EmployeeListObservable).next(this.EmployeeList);
        }
    }

    public GetEmployee(empId: number): Employee {
        return this.EmployeeList.find(x => x.Id == empId);
    }
}


